}
.advantages__item--icons-decorated {
  box-sizing: border-box;
  width: 87.5%;
  margin: 0 auto;
  padding-top: 5px;
  padding-left: 47px;
  text-align: start;
  background-repeat: no-repeat;
  background-position: 0 10px;
}
.advantages__item--house {
  background-image: url("../img/icons/icon-house.svg");
  background-size: 39px 37px;
}
.advantages__item--food {
  padding-left: 52px;
  background-image: url("../img/icons/icon-burger.svg");
  background-size: 40px 35px;
}
.advantages__item--gift {
  background-image: url("../img/icons/icon-gift.svg");
  background-size: 32px 38px;
}
.advantages__item-picture {
  width: 100%;
  height: 260px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .advantages__item {
    min-height: 256px;
  }
  .advantages__item--wide {
    display: grid;
    grid-template-columns: repeat(3, minmax(256px, 1fr));
    grid-column: span 3;
  }
  .advantages__item--wide:first-child::before {
    content: none;
  }
  .advantages__item--wide-reverse .advantages__item-picture {
    order: -1;
  }
  .advantages__item--icons-decorated {
    box-sizing: content-box;
    width: auto;
    min-height: 220px;
    padding-top: 110px;
    padding-left: 0;
    text-align: center;
    background-position: center 60px;
  }
  .advantages__item--house {
    background-size: 75px 72px;
  }
  .advantages__item--food {
    background-size: 76px 68px;
  }
  .advantages__item--gift {
    background-size: 64px 75px;
  }
  .advantages__item-text-content {
    width: 195px;
    margin: 0 auto;
    margin-top: 42px;
  }
  .advantages__item-picture {
    grid-column: span 2;
    object-fit: cover;
    width: 100%;
    height: 256px;
  }
  .advantages__item-picture--scale-position {
    object-position: top calc(0px + 10%) right 0;
  }
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, minmax(256px, 1fr));
  }
}
.button {
  display: block;
  padding: 30px;
  padding-top: 18px;
  padding-bottom: 17px;
  font-family: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
}
.control {
  display: flex;
  align-items: center;
  width: 224px;
  height: 46px;
}
.control__input {
  position: relative;
  margin: 0;
  appearance: none;
}
.control__input::before {
  display: block;
  content: "";
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
}
.control__input:checked::before {
  background-image: url("../img/icons/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 22px 16px;
}
.control__input[type="radio"]::before {
  border-radius: 50%;
}
.control__input[type="radio"]:checked::before {
  background-image: none;
}
.control__input[type="radio"]:checked::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  margin: auto;
  content: "";
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
}
.control__label {
  margin-left: 16px;
}
.copyright {
  text-transform: uppercase;
}
.copyright__link {
  text-decoration: none;
  color: inherit;
}
.copyright__link:hover {
